/* ALL BUTTON STYLES */
.button {
  color: #ffffff;
  letter-spacing: 1.1px;
  font-weight: 700;
  font-size: 1rem;
  height: 42px;
  white-space: nowrap;
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 155px;
  background-color: #6a983c;
  border-radius: 12px;
  border: 2px solid #46760a;
  margin-left: 5px;
  cursor: pointer;
}
.button a {
  color: #ffffff;
}

.suffix {
  margin-left: 5px;
}

/* PAGINATION BUTTON */
.pagination {
  max-width: fit-content;
}

/* BUY BIG BUTTON */
.buyBig {
  width: 165px;
  cursor: pointer;
}
.plus {
  font-size: 26px;
  margin-right: 4px;
  color: #92c064;
  position: relative;
  top: -2px;
}
/* DISABLED */
.disabled {
  opacity: 0.6;
}

@media (max-width: 1024px) {
  .buyBig {
    width: fit-content;
  }
}

@media (max-width: 768px) {
  .detail,
  .pagination {
    width: 100%;
    height: 36px;
    font-size: 0.9rem;
  }
}
@media (max-width: 549px) {
  .detail,
  .pagination {
    width: 100%;
    height: 36px;
    font-size: 0.8rem;
  }
}
